.dpc {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  justify-content: center;
  vertical-align: baseline;
}
pre {
  display: block;
  font-family: monospace;
  white-space: pre;
  margin: 1em 0;
}

:root {
  --text-color: #f0f4f5;
  --backgroud-color: #480619;
  --accent-color: pink;
}

#root {
  display: flex;
  justify-content: center;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: inheirt;
  box-sizing: border-box;
  /* outline: 0; */
  outline: none;
  border: 0;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  -webkit-box-sizing: inheirt;
  /* box-sizing: inheirt; */
  line-height: 1.2;
  text-align: center;
}

a {
  text-decoration: none;
}

li {
  list-style-type: none;
}
